@font-face {
  font-family: 'bahnschrift';
  src: local('bahnschrift'), url(./assets/fonts/bahnschrift.ttf) format('truetype');
}

@font-face {
  font-family: 'calibri';
  src: local('calibri'), url(./assets/fonts/calibri.ttf) format('truetype');
}

@font-face {
  font-family: 'fredoka-one';
  src: local('fredoka-one'), url(./assets/fonts/fredoka-one.ttf) format('truetype');
}

@font-face {
  font-family: 'geomanist';
  src: local('geomanist'), url(./assets/fonts/geomanist.ttf) format('truetype');
}

@font-face {
  font-family: 'signature';
  src: local('signature'), url(./assets/fonts/signature.ttf) format('truetype');
}

@font-face {
  font-family: 'roboto';
  src: local('roboto'), url(./assets/fonts/roboto.ttf) format('truetype');
}

@font-face {
  font-family: 'montserrat';
  src: local('montserrat'), url(./assets/fonts/montserrat.ttf) format('truetype');
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
  'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
  background-color: #f5f4f9;
  color: #1a1a1a;
}

::-webkit-scrollbar {
  width: 0.6rem;
}

::-webkit-scrollbar-track {
  background: #1f2937;
  border-radius: 0.42rem;
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 0.42rem;
  background: linear-gradient(to top, #e14fad 0%, #f9d423 100%);
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
  monospace;
}
